.activity-popover-img {
  width: 100%;
  height: 140px;
  object-fit: cover;
}

.arrow-hover {
  transition: all .3s linear;
}

.arrow-hover:hover {
  transform: translateX(8px);
}

.dot-menu {
  width: 15px;
  height: 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.dot {
  background: var(--bs-body-color);
  width: 4px;
  height: 4px;
  border-radius: 50%;
  transition: all 0.6s cubic-bezier(0.66, -0.45, 0.35, 1.39);
}

.line2 {
  position: relative;
  bottom: 4px;
  transform: rotateZ(-45deg);
}

.line1 {
  position: relative;
  top: 3px;
  transform: rotateZ(45deg);
}

.dot-menu.active .dot1 {
  transform: translateY(12px);
}

.dot-menu.active .dot2 {
  transform: translateY(-12px);
}

.dot-menu.active .line1 {
  width: 20px;
  border-radius: 0px;
  top: 4px;
  transform: rotateZ(45deg);
}

.dot-menu.active .line2 {
  width: 20px;
  transform: rotateZ(-45deg);
  border-radius: 0px;
}

.activity-popover {
  position: absolute;
  top: 115px;
  left: 95px;
  border-radius: 15px;
  background: var(--bs-white);
  box-shadow: 0 0 9px rgba(45, 45, 45, 0.15);
}

.activity-popover-tail {
  position: absolute;
  display: block;
  top: -5px;
  left: 0;
  filter: drop-shadow(-2px -3px 5px rgba(75, 59, 59, 0.15));
}

.activity-popover-content {
  width: 141px;
  height: 40px;
  overflow: hidden;
  transition: all .3s linear;
}

.activity-popover-content.active {
  transition: all .3s .15s linear;
  height: 412px;
}

.activity-popover-content.active > .activity-popover-defaule {
  transition: all .1s linear;
  opacity: 0;
}

.activity-popover-content.active > .activity-popover-card {
  transition: all .3s .15s linear;
  opacity: 1;
}

.activity-popover-content.d-none + .activity-popover-tail {
  display: none;
}

@media screen and (min-width: 1400px) {
  .activity-popover-content {
    width: 304px;
    height: 112px;
  }
}

.activity-popover-defaule {
  z-index: 1;
  transition: all .1s .18s linear;
}

.activity-popover-card {
  z-index: -1;
  opacity: 0;
  transition: all .3s linear;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  display: block;
  transition: .3s;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  opacity: 1;
}

body {
  font-family: "Noto Serif SC", serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-weight: 700;
  font-style: normal;
  line-height: 1.5;
  background: #FEF9E4;
  color: #131313;
}

.font-noto {
  font-family: 'Noto Sans TC', serif;
  font-weight: 400;
  font-style: normal;
}

.menu {
  min-height: calc(100vh);
  overflow: hidden;
}

:root.dark-mode .color-theme {
  background-image: url(../images/color_theme_toggle_moon_LG.svg);
}

.color-theme {
  width: 66px;
  height: 34px;
  background-image: url(../images/color_theme_toggle_sun_LG.svg);
  cursor: pointer;
  transition: .3s all ease;
}

.popup-content-bounce-in-up {
  -webkit-animation-name: popup_content_bounce_in_up;
  -moz-animation-name: popup_content_bounce_in_up;
  -o-animation-name: popup_content_bounce_in_up;
  animation-name: popup_content_bounce_in_up;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@keyframes popup_content_bounce_in_up {
  0% {
    opacity: 0;
    transform: translateY(2000px);
  }
  60% {
    opacity: 1;
    transform: translateY(-30px);
  }
  80% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}

.sticky-popup {
  position: relative;
  width: 100%;
  bottom: -86.3%;
  cursor: pointer;
}

.sticky-popup.open {
  bottom: 0;
}

.sticky-popup.open .popup-content {
  opacity: 1;
}

.sticky-popup.open .popup-header {
  transform: translateY(0.5px);
  transition: .3s;
}

.open_sticky_popup {
  -webkit-transition: bottom .6s;
  -moz-transition: bottom .6s;
  -o-transition: bottom .6s;
  transition: bottom .6s;
}

.popup-header {
  transition: .1s;
  transform: translateY(10px);
  border: 2px solid var(--bs-body-color);
  background: var(--bs-gray-100);
}

.popup-header:hover {
  transform: translateY(0.5px);
  transition: .3s;
}

.popup-content {
  width: 100%;
  height: 40%;
  overflow-x: hidden;
  overflow-y: auto;
  background: var(--bs-gray-100);
  opacity: 0;
  transition: opacity .8s;
}

.popup-body {
  height: 10%;
  overflow-x: hidden;
}

.popup-content-trapezoid {
  width: 100%;
  border-top: 8px solid var(--bs-body-color);
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
  transition: .3s;
}

.circle-item {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  align-items: center;
  font-weight: 400;
  color: #cccccc;
}

.circle-item.active {
  color: var(--bs-body-color);
}

.circle-item.active .circle-item-triangle {
  border-color: transparent transparent transparent var(--bs-body-color);
}

.circle-item.active .circle-item-number {
  color: var(--bs-body-color);
}

.circle-item.active .circle-item-number::after {
  border-color: var(--bs-body-color);
  background: var(--bs-secondary);
  color: var(--bs-body-color);
}

.circle-item.done {
  color: var(--bs-body-color);
}

.circle-item.done .circle-item-triangle {
  border-color: transparent transparent transparent var(--bs-body-color);
}

.circle-item.done .circle-item-number {
  color: var(--bs-white);
}

.circle-item.done .circle-item-number::after {
  border-color: var(--bs-body-color);
  background: var(--bs-gray-900);
  color: var(--bs-white);
}

.circle-item-number {
  z-index: 1;
  margin-right: 18px;
}

.circle-item-number::after {
  content: '';
  position: absolute;
  top: .7rem;
  left: 83%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border: 2px solid var(--bs-secondary);
  border-radius: 50%;
  background-color: var(--bs-primary);
  transition: 0.3s linear;
  z-index: -1;
}

.circle-item-triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 0 5px 8.7px;
  border-color: transparent transparent transparent var(--bs-gray-500);
}

.dog-scooter {
  width: 100px;
  height: 80px;
  position: absolute;
  bottom: calc(0% - 30px);
  right: -40px;
  z-index: 100;
  transform: translateY(0) scaleX(-1);
  transition: 0.4s ease;
}

.dog-scooter .a {
  fill: #d980bb;
}

.dog-scooter .b {
  fill: #2d2d2d;
}

.dog-scooter .c {
  fill: #fff;
}

.dog-scooter .d {
  fill: #fcc11e;
}

.dog-scooter .e {
  fill: #335d2e;
}

.dog-scooter .f {
  fill: #0497b9;
}

.dog-scooter .g {
  fill: #ec132f;
}

.dog-scooter .h {
  fill: #fec920;
}

.progress-bar {
  height: calc(100% - 1px);
  width: 4px;
  position: absolute;
  right: 20px;
  outline: 1px solid var(--bs-body-color);
  background-color: transparent;
}

.progress {
  height: 0%;
  width: 4px;
  position: absolute;
  bottom: 0;
  background-color: var(--bs-secondary);
  transition: 0.4s ease;
}
